.testimonials {
  &__item {
    margin-bottom: 48px;
    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 28px;
    @include onDesktop {
      margin-bottom: 32px;
    }
  }

  &__photo {
    width: 100%;
    grid-column: 2 / 4;
    border-radius: 50%;
    transition: transform 0.3s;
    &:hover {
      transform: scale(1.2);
    }
  }

  &__name {
    margin: 0 0 4px;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;

    text-align: center;
    color: $colorBlue;
  }

  &__role {
    margin: 0 0 4px;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }
  
  &__about {
    text-align: center;
  }
}

.link {
  color: $colorBlue;
  text-decoration: none;
  transition: color 0.3s;
  &:hover {
    color: rgb(22, 170, 219);
  }
}
